@import url(https://fonts.googleapis.com/css2?family=BioRhyme:wght@200;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center;background-image:url("http://localhost:3000/background_1.png");background-size:cover;font-family:'BioRhyme', serif}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-container{background-color:rgba(40,44,52,0.42);min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.header{display:grid;grid-gap:15px;gap:15px;color:#FCCD04}.score{display:flex;justify-content:space-between}.competitor-section{display:grid;grid-template-columns:1fr 1fr;color:#FFE400}.hero-section{display:grid;grid-template-columns:1fr;justify-content:left;text-align:left;margin-left:20px;margin-top:10px;grid-gap:5px;gap:5px}.pc-section{display:grid;justify-content:right;margin-right:20px;margin-top:10px;grid-gap:5px;gap:5px}.hero-section--name{padding-left:10px}.pc-section--name{padding-right:10px}.radio{display:none}.pc-choice-container{width:120px;height:120px}.symbols{display:flex;justify-content:center;grid-gap:60px;gap:60px}.symbol-container{height:175px;display:grid;justify-content:center;align-items:center}.chosen-symbol{height:100px;border-radius:50%;transition:box-shadow 0.2s}.chosen-symbol:hover{box-shadow:rgba(50,50,93,0.25) 0px 30px 60px -12px inset,rgba(0,0,0,0.3) 0px 18px 36px -18px inset}.chosen-symbol--active{transition:0.5s;height:130px;background-color:teal}.chosen-symbol--pc{-webkit-appearance:meter;height:130px;border-radius:50%;background-color:#C3073F}.character-image--cropper{width:80px;height:80px;position:relative;overflow:hidden;border-radius:50%;box-shadow:0 0 4px 2px #e3e3e3,0 0 5px 3px gold,0 0 7px 4px #06836e}.chosen-character-image{display:inline;margin:0 auto;height:155%;position:absolute;top:-10%;right:-40%;width:auto}.pc-image{display:inline;margin:0 auto;height:120%;position:absolute;top:-7%;right:-93%;width:auto}

.modal{height:100vh;width:100vw;background-color:rgba(0,0,0,0.9);position:fixed;top:0;left:0;display:grid;justify-content:center;align-items:center;-webkit-transform:scale(0);transform:scale(0)}.modal.active{-webkit-transform:scale(1);transform:scale(1)}.modal__content-one{background-color:#F5E6CC;color:#C3073F;width:60%;margin:10px auto 10px 60px;min-width:400px;padding:5px;border-radius:15px;text-transform:uppercase}.modal__content-two{width:70%;min-width:400px;margin:0 50px 10px auto;padding:20px;border-radius:15px;background-color:#F5E6CC;color:#14A76C}.modal__content-three{width:70%;min-width:400px;margin:0 auto;padding-left:40px;padding-right:40px;border-radius:15px;background-color:#F5E6CC;color:#C3073F}.heading{font-size:1.5rem}.heading--introduction{font-size:0.75rem}.subheading{font-size:1.255rem}.paragraph{font-size:1rem;text-align:justify}.paragraph__item{margin:0 0 0 30px}.character--container{display:flex;justify-content:center;grid-gap:20px;gap:20px}.character{display:grid;grid-template-columns:1fr;grid-gap:5px;gap:5px}.character-image{height:100px;border-radius:55%;box-shadow:rgba(0,0,0,0.16) 0px 1px 4px;opacity:0.7;transition:opacity 0.2s}.character-image:hover{opacity:1;outline:3px solid gold;box-shadow:rgba(50,50,93,0.25) 0px 50px 100px -20px,rgba(0,0,0,0.3) 0px 30px 60px -30px,rgba(10,37,64,0.35) 0px -2px 6px 0px inset;transition:outline-width 0.3s}.character-image:active{outline:5px solid gold;transition:outline-width 0.1s}

.button-component{padding:15px 20px;border:1px solid transparent;font-family:inherit;font-size:inherit;color:#FCCD04;transition:0.2s}.button-component:hover{padding-left:60px;padding-right:60px;border-radius:-5px}.button-component:active{-webkit-animation:shake 0.5s;animation:shake 0.5s;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}@-webkit-keyframes shake{0%{-webkit-transform:translate(1px, 1px) rotate(0deg);transform:translate(1px, 1px) rotate(0deg)}10%{-webkit-transform:translate(-1px, -2px) rotate(-1deg);transform:translate(-1px, -2px) rotate(-1deg)}20%{-webkit-transform:translate(-3px, 0px) rotate(1deg);transform:translate(-3px, 0px) rotate(1deg)}30%{-webkit-transform:translate(3px, 2px) rotate(0deg);transform:translate(3px, 2px) rotate(0deg)}40%{-webkit-transform:translate(1px, -1px) rotate(1deg);transform:translate(1px, -1px) rotate(1deg)}50%{-webkit-transform:translate(-1px, 2px) rotate(-1deg);transform:translate(-1px, 2px) rotate(-1deg)}60%{-webkit-transform:translate(-3px, 1px) rotate(0deg);transform:translate(-3px, 1px) rotate(0deg)}70%{-webkit-transform:translate(3px, 1px) rotate(-1deg);transform:translate(3px, 1px) rotate(-1deg)}80%{-webkit-transform:translate(-1px, -1px) rotate(1deg);transform:translate(-1px, -1px) rotate(1deg)}90%{-webkit-transform:translate(1px, 2px) rotate(0deg);transform:translate(1px, 2px) rotate(0deg)}100%{-webkit-transform:translate(1px, -2px) rotate(-1deg);transform:translate(1px, -2px) rotate(-1deg)}}@keyframes shake{0%{-webkit-transform:translate(1px, 1px) rotate(0deg);transform:translate(1px, 1px) rotate(0deg)}10%{-webkit-transform:translate(-1px, -2px) rotate(-1deg);transform:translate(-1px, -2px) rotate(-1deg)}20%{-webkit-transform:translate(-3px, 0px) rotate(1deg);transform:translate(-3px, 0px) rotate(1deg)}30%{-webkit-transform:translate(3px, 2px) rotate(0deg);transform:translate(3px, 2px) rotate(0deg)}40%{-webkit-transform:translate(1px, -1px) rotate(1deg);transform:translate(1px, -1px) rotate(1deg)}50%{-webkit-transform:translate(-1px, 2px) rotate(-1deg);transform:translate(-1px, 2px) rotate(-1deg)}60%{-webkit-transform:translate(-3px, 1px) rotate(0deg);transform:translate(-3px, 1px) rotate(0deg)}70%{-webkit-transform:translate(3px, 1px) rotate(-1deg);transform:translate(3px, 1px) rotate(-1deg)}80%{-webkit-transform:translate(-1px, -1px) rotate(1deg);transform:translate(-1px, -1px) rotate(1deg)}90%{-webkit-transform:translate(1px, 2px) rotate(0deg);transform:translate(1px, 2px) rotate(0deg)}100%{-webkit-transform:translate(1px, -2px) rotate(-1deg);transform:translate(1px, -2px) rotate(-1deg)}}

.modal{height:100vh;width:100vw;background-color:rgba(0,0,0,0.9);position:fixed;top:0;left:0;display:grid;justify-content:center;align-items:center;-webkit-transform:scale(0);transform:scale(0)}.modal.active{-webkit-transform:scale(1);transform:scale(1)}.modal__content{width:70%;min-width:500px;margin:0 auto;padding-left:40px;padding-right:40px;padding-bottom:40px;border-radius:15px;background-color:#F5E6CC;color:#C3073F}.credits{padding-top:40px;display:grid;text-align:end;font-size:0.5rem}.link{text-decoration:none;color:teal}

